
.triangle:hover {
  opacity: 0.9;
} 

#editor-outer {
  position: fixed;
  top: 10vh;
  left: 10vw;
  padding: 13px;
  max-width: 283px;
  background-color: #8f8f8f;
  opacity: 0.6;
  font-family: sans-serif;
  border-radius: 10px;
  border: 1px solid #666;
  min-height:150px;
} 

#editor-outer:hover {
  opacity: 1;
} 

#editor-colors,
#editor-options {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
} 

.editor-color {
  margin: 2px 1px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
  border-width: 0;
  border: 1px solid #333;
} 

.editor-color:hover {
  cursor: pointer;
  -webkit-transform: scale(2);
          transform: scale(2);
} 

.editor-color-selected {
  border: 3px solid #FFF;
} 

.editor-color-selected-alt {
  border: 3px dotted #FFF;
} 

.editor-color:focus {
  outline: none;
} 

.editor-option,
.editor-option-input,
.editor-option-input-wide,
.editor-option-input-xwide {
  font-size: 14px;
  font-weight: 600;
  color: #000;
  background-color: #AAA;
  padding: 2px 3px;
  margin: 4px 1px;
  border-radius: 5px;
  border: 1px solid #444;
} 

.editor-option-input,
.editor-option-input-wide,
.editor-option-input-xwide {
  background-color: rgb(218, 218, 218);
  width: 46px;
} 

.editor-option-input-wide {
  width: 110px;
} 

.editor-option-input-xwide {
  width: 130px;
} 

.editor-option:hover {
  background-color: #CCC;
  cursor: pointer;
} 

.editor-option:active {
  color: #AAA;
} 

.editor-label {
  display: flex;
  align-items: center;
  margin: -2px 0px;
} 

#editor-frame-timing {
  height: 200%;
  border-radius: inherit;
  overflow-y: auto;
  background: inherit;
  position: absolute;
  top: 0px;
  left: 102%;
} 

#editor-frame-timing-inner {
  height: auto;
  width: 340px;
  padding: 15px;
} 

.editor-frame-timing-row {
  width: 305px;
  font-size: 13px;
  display: flex;
  flex-flow: row nowrap;
  padding: 3px 15px;
} 

.editor-frame-timing-item,
.editor-frame-timing-itemlabel {
  width: 100px;
  height: 20px;
  margin: 0 1px;
  box-sizing: border-box;
} 

.editor-frame-timing-itemlabel {
  padding-left: 5px; 
} 

#editor-frame-timing-minimap {
  width: 100%;
  background-color: rgb(199, 199, 199);
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
} 

.editor-frame-timing-minimap-part {
  height: 40px;
  margin: 0px;
} 

#editor-use-tgroups-outer {
  position: absolute;
  top: 42px;
} 

code {
  background-color: rgba(0, 0, 0, 0.8);
}